export default {
	data() {
		return {
			nickName:'测试',
			requireList:[],
			userInfo:{},
			blank:true,
			openid:''
		}
	},
	onLoad () {
		this.getOpenid();
		this.getUser()
	},
	methods: {
		async list(openid){
			
			var data={
				'type':1,
				'openid':openid
			}
			console.log(this.openid);
			var res =await this.$request.post('/require',data);
			console.log(res.result);
			 this.requireList = res.result;
			if(this.requireList.length > 0){
				this.blank = false
			}
		},
		getUser(){
			uni.getStorage({
				key:'userInfo',
				success: (res) => {
					console.log(res.data);
					this.userInfo = res.data
				},
				fail: (err) => {
					uni.getUserInfo({
						success: (userInfo) => {
							this.userInfo = userInfo.data
						}
					})
				}
			});
		},
		getOpenid(){
			uni.getStorage({
				key:'openid',
				success: (res) => {
					this.list(res.data)
					
				},
				fail: (err) => {
					console.log(err)
				}
			})
		}
		
	}
}
